import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';

import Demo1 from './demo1';
import Demo2 from './demo2';
import Demo3 from './demo3';
import Demo4 from './demo4';
import Demo5 from './demo5';
import Demo6 from './demo6';


class Page extends PureComponent {
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>Card </h1>
                <blockquote className='page-tip'>
                    <br/>
                    
                </blockquote>

                <h1 className='page-h1'></h1>

                <Label className='code-col'>
                    <Label className='code-box'>
                        {<Demo1 />}
                    </Label>
                </Label>

                <Label className='code-col'>
                    <Label className='code-box'>
                        {<Demo2 />}
                    </Label>
                </Label>

                <Label className='code-col'>
                    <Label className='code-box'>
                        {<Demo3 />}
                    </Label>
                </Label>

                <Label className='code-col'>
                    <Label className='code-box'>
                        {<Demo4 />}
                    </Label>
                </Label>

                <Label className='code-col'>
                    <Label className='code-box'>
                        {<Demo5 />}
                    </Label>
                </Label>

                <Label className='code-col'>
                    <Label className='code-box'>
                        {<Demo6 />}
                    </Label>
                </Label>

                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>Card</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'title', desc: '', type: 'string', default: ''},
                    {param: 'tools', desc: '', type: 'string/ReactElement', default: ''},
                    {param: 'border', desc: '', type: 'boolean', default: 'true'},
                    {param: 'loadding', desc: '', type: 'boolean', default: 'false'},
                    {param: 'bodyStyle', desc: '', type: 'object', default: ''}
                ]}></Table>

                <h3 className='page-h3'>Methods</h3>

                <ul className='code-methods'>
                    <li>
                        <i>setTitle(title)</i>
                        <span>Card</span>
                        <ul>
                            <li>
                                title {'String'} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>setContent(content)</i>
                        <span>Card</span>
                        <ul>
                            <li>
                                content {'String/ReactElement'} React
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        );
    }
}

export default Page;



